.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: url("../asset/background.png"); /* 배경 이미지 경로 */
  background-size: cover; /* 이미지를 화면 크기에 맞게 조정 */
  background-position: center; /* 이미지를 중앙에 위치 */
  background-repeat: no-repeat; /* 이미지 반복 방지 */
  height: 100vh;
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
}

.appTitle {
  color: white;
  font-family: "Pretendard Medium", sans-serif;
  font-size: 32px;
  margin-top: 20px;
  text-align: center;
}

.appName {
  background: linear-gradient(-45deg, #ffbec8, #5cbf84);
  color: transparent;
  -webkit-background-clip: text;
  font-family: "Pretendard Bold", sans-serif;
  font-size: 32px;
  margin-top: 16px;
  text-align: center;
}

.inputField {
  width: calc(100% - 46px);
  margin: 16px 23px;
  padding: 12px;
  font-family: "Pretendard Regular", sans-serif;
  font-size: 20px;
  color: #979797;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.submitButton {
  width: 368px;
  height: 61px;
  background-color: #ffa9b5;
  color: #fff;
  font-family: "Pretendard Semibold", sans-serif;
  font-size: 24px;
  margin-bottom: 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.divider {
  width: calc(100% - 46px);
  height: 1.5px;
  background-color: #ccc;
  margin-bottom: 16px;
}

.registerText {
  color: white;
  font-size: 20px;
  margin-bottom: 40px;
  cursor: pointer;
  text-align: center;
}
