.container {
  padding: 16px;
}

.toolbar {
  display: flex;
  align-items: center;
  background-color: #f8f8f8;
  padding: 8px 16px;
  border-bottom: 1px solid #ddd;
}

.backButton {
  background: none;
  border: none;
  cursor: pointer;
}

.backIcon {
  width: 24px;
  height: 24px;
}

.toolbarTitle {
  flex-grow: 1;
  text-align: center;
  font-size: 20px;
}

.card {
  position: relative;
  margin: 16px 0;
}

.cardImage {
  width: 100%;
  height: 224px;
  object-fit: cover;
  border-radius: 10px;
}

.cardIcon {
  position: absolute;
  right: 16px;
  bottom: 16px;
  width: 24px;
  height: 24px;
}

.section {
  margin: 16px 0;
  padding: 16px;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-sizing: border-box; /* 박스 모델을 border-box로 설정 */
}

.titleText {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 8px;
  white-space: normal; /* 텍스트 줄 바꿈 허용 */
  word-break: break-word; /* 긴 단어를 자동으로 줄 바꿈 */
}

.nameText,
.telText,
.contentText,
.moneyContent,
.cautionContent,
.conditionContent {
  font-size: 16px;
  font-weight: 500;
  margin: 4px 0;
  white-space: normal; /* 텍스트 줄 바꿈 허용 */
  word-break: break-word; /* 긴 단어를 자동으로 줄 바꿈 */
}

.moneyText,
.cautionText,
.conditionText {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 8px;
  white-space: normal; /* 텍스트 줄 바꿈 허용 */
  word-break: break-word; /* 긴 단어를 자동으로 줄 바꿈 */
}

.cautionText {
  color: #ff4848; /* 주의사항 텍스트를 빨간색으로 설정 */
}
.cautionContent {
  color: #000;
}
.submitButtonContainer {
  margin-top: 16px;
}

.submitButton {
  width: 100%;
  padding: 16px;
  background-color: #ffbec8;
  color: #fff;
  font-size: 20px;
  border-radius: 8px;
  cursor: pointer;
  border: none;
}
